<template>

  <div class="app-container">
    <p></p>
    <el-form ref="form" :model="form" label-width="180px">

      <el-form-item label="()">
        <!---->
        <el-col :span="20">
          <el-input v-model="form.account"  placeholder="" />
        </el-col>
      </el-form-item>

      <el-form-item label="">
        <el-col :span="20">
          <el-input v-model="form.name"/>
        </el-col>
      </el-form-item>

      <!--<el-form-item label="()">-->
        <!--<el-col :span="20">-->
          <!--&lt;!&ndash;&ndash;&gt;-->
          <!--<el-upload-->
            <!--class="upload-file"-->
            <!--drag-->
            <!--accept="image/png,image/gif.image/jpg,image/jpeg"-->
            <!--:limit=1-->
            <!--list-type="picture-card"-->
            <!--:action="doUpload()"-->
            <!--:before-upload="beforeUpload"-->
            <!--ref="newupload"-->
            <!--multiple-->
            <!--:auto-upload="false">-->
            <!--<i class="el-icon-upload"></i>-->
            <!--&lt;!&ndash;<div class="el-upload__text"><em></em></div>&ndash;&gt;-->
          <!--</el-upload>-->
        <!--</el-col>-->
      <!--</el-form-item>-->


      <el-form-item label="">
        <el-col :span="20">
          <el-input v-model="form.class"/>
        </el-col>
      </el-form-item>

      <el-form-item label="">
        <el-col :span="20">
          <el-input v-model="form.blief"/>
        </el-col>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button @click="onCancel"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        form: {}
      }
    },
    methods: {
      onSubmit() {
        // this.$refs.newupload.submit();
        if (this.form.account != null){
          this.$axios.post("/admin/changeDoctor", {account: this.form.account, name: this.form.name, class: this.form.class, blief: this.form.blief})
            .then(data => {
              if (data.data.status === "2000") {
                this.$message('');
              } else {
                this.openError(data.data.message);
              }
            }).catch(()=>{this.openError("");});
        }else {
          this.openWarn('');
        }

      },
      onCancel() {
        this.$message({
          message: '',
          type: 'warning'
        });
        this.form = {};
      },
      beforeUpload(file){
        let fd = new FormData();
        fd.append('file',file);//
        fd.append('name',this.form.name);//
        fd.append('account',this.form.account);
        fd.append('class',this.form.class);
        fd.append('blief',this.form.blief);
        this.$axios.post('/admin/changeDoctor',fd).then(function(res){
          alert('');
        })
      },
      newSubmitForm(){
        //
        this.$refs.newupload.submit();
      },
      doUpload(){
        // url
        return "/useless";
      },
      openWarn(message) {
        this.$alert(message, '', {
          confirmButtonText: '',
          callback: action => {
            // 
          }
        });
      },
      openError(message) {
        this.$alert(message, '', {
          confirmButtonText: '',
          callback: action => {
            // 
          }
        });
      },
    },

  }
</script>

<style scoped>
  .line{
    text-align: center;
  }
</style>




