// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

// cookie
import * as cookieApi from './api/cookie'
Vue.prototype.cookieApi = cookieApi;

// 
import * as userApi from './api/localstorage'
Vue.prototype.userApi = userApi;

// axios
import axios from 'axios'
// axios 
axios.defaults.timeout = 5000;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
// axios.defaults.headers.post['Authorization'] = 'bearer ' + Vue.prototype.cookieApi.getTokenCookie();
// configindex.js
// axios.defaults.baseURL = '/api';
axios.defaults.baseURL = '/';
// http request Cookie
// axios.interceptors.request.use(
//   config => {
//     const token = cookieApi.getTokenCookie(); //Cookie
//     config.data = JSON.stringify(config.data);
//     if (token) {
//       config.params = {'token': token} //
//     }
//     return config;
//   },
//   err => {
//     return Promise.reject(err);
//   }
// );
Vue.prototype.$axios = axios;

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import { Message } from 'element-ui';
Vue.prototype.$message = Message;
Vue.use(ElementUI);

// 
import 'font-awesome/css/font-awesome.min.css'

// websocket
import * as socketApi from './api/socket'
Vue.prototype.socketApi = socketApi;

Vue.config.productionTip = false;

// 
router.beforeEach((to, from, next) => {
  // 
  if (to.matched.some(record => record.meta.isLogin)){
    // token
    if (sessionStorage.getItem("auth")) {
      //console.log(sessionStorage.getItem("auth"), to.meta.auth)
      if (sessionStorage.getItem("auth") == to.meta.auth){
        next();
      }
      else{
        // 
        next({
          path: '/login',
          // path
          query: {redirect: to.fullPath}
        });
      }
    }
    else {
      next({
        path: '/login',
        // path
        query: {redirect: to.fullPath}
      })
    }
  }
  else {
    next();
  }
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  render: h => h(App),
  // 
}).$mount('#app');




