<template>

  <div class="app-container">
    <p></p>
    <div id="agora_remote"></div>
    <div id="agora_local"></div>
    <el-button @click="setDown"></el-button>
  </div>
</template>

<script>
  // 
  import AgoraRTC from 'agora-rtc-sdk'
  //  AgoraRTC.createClient  Client  mode  codec

  var client = AgoraRTC.createClient({mode: 'live', codec: "h264"});
  let room_id = null;

  export default {
    data() {
      return {
        form: {},

      }
    },
    created(){

      room_id = this.$route.query.id;
      if (room_id == null){
        this.$message({
          message: "",
          type: 'error'
        });
        this.$router.push('/doctor');
      }

      //  Client  App ID  client.init  Client

      client.init('df82778c111c4c57aa3f13fbca9f40cc', function () {
        console.log("AgoraRTC client initialized");
      }, function (err) {
        console.log("AgoraRTC client init failed", err);
      });
      //  Client   client.join 
      //
      //  client.join 
      //
      // tokenOrKey null token  Channel Key  
      // channel
      // uid ID   ID  null uid

      let uid = null;
      client.join(null, room_id, null, function(id) {
        uid = id;
        console.log("User " + uid + " join channel successfully");
        localStreamStart();
      }, function(err) {
        console.log("Join channel failed", err);
      });
      // uid
      function localStreamStart() {
        var localStream = AgoraRTC.createStream({
          streamID: uid,
          audio: true,
          video: false,
          screen: false}
        );
        //  stream.init 
        localStream.init(function() {
          console.log("getUserMedia successfully");
          localStream.play('agora_local');

          //  client.publish 

          client.publish(localStream, function (err) {
            console.log("Publish local stream error: " + err);
          });

        }, function (err) {
          console.log("getUserMedia failed", err);
        });

        client.on('stream-published', function (evt) {
          console.log("Publish local stream successfully");
        });

        // 
        //
        //  client.on('stream-added') , 
        //  client.subscribe 

        client.on('stream-added', function (evt) {
          var stream = evt.stream;
          console.log("New stream added: " + stream.getId());

          client.subscribe(stream, function (err) {
            console.log("Subscribe stream failed", err);
          });
        });
        client.on('stream-subscribed', function (evt) {
          var remoteStream = evt.stream;
          console.log("Subscribe remote stream successfully: " + remoteStream.getId());
          // remoteStream.play('agora_remote' + remoteStream.getId());
          remoteStream.play('agora_remote');
        })
      }
    },
    methods: {
      openWarn(message) {
        this.$alert(message, '', {
          confirmButtonText: '',
          callback: action => {
            // 
          }
        });
      },
      openError(message) {
        this.$alert(message, '', {
          confirmButtonText: '',
          callback: action => {
            // 
          }
        });
      },
      setDown(){
        client.leave(function () {
          console.log("Leave channel successfully");
        }, function (err) {
          console.log("Leave channel failed");
        });
        this.socketApi.sendSock({code:"2012", message:""});
        this.$message({
          type: 'success',
          message: '!'
        });
        this.$router.push('/doctor')
      }
    }
  }
</script>

<style scoped>
  .line{
    text-align: center;
  }
</style>




