<template>

  <div class="app-container">
    <p></p>
    <el-form ref="form" :model="form" label-width="120px">

      <el-form-item label="" >
        <!---->
        <el-col :span="20">
          <el-input v-model="form.account" placeholder=""/>
        </el-col>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="form.type" placeholder="">
          <el-option label="" value="changeName"/>
          <el-option label="" value="changPass"/>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-col :span="20">
          <el-input v-model="form.value"/>
        </el-col>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button @click="onCancel"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        form: {}
      }
    },
    methods: {
      onSubmit() {
        if (this.form.account != null && this.form.type != null && this.form.value != null) {
          this.$axios.post("/admin/changeAccount", {account: this.form.account, type: this.form.type, value: this.form.value})
            .then(data => {
              if (data.data.status === "2000") {
                this.$message('');
                this.form = {};
              } else {
                this.openError(data.data.message);
              }
            }).catch(()=>{this.openError("");});
        }else {
          this.openWarn('');
        }

      },
      onCancel() {
        this.$message({
          message: '',
          type: 'warning'
        });
        this.form = {};
      },
      openWarn(message) {
        this.$alert(message, '', {
          confirmButtonText: '',
          callback: action => {
            // 
          }
        });
      },
      openError(message) {
        this.$alert(message, '', {
          confirmButtonText: '',
          callback: action => {
            // 
          }
        });
      },
    }
  }
</script>

<style scoped>
  .line{
    text-align: center;
  }
</style>




