<template>

    <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm login-container">
        <h3 class="title"></h3>
        <el-form-item prop="account">
            <el-input type="text" v-model="ruleForm2.account" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="checkPass">
            <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-checkbox v-model="checked" checked class="remember"></el-checkbox>
        <el-form-item style="width:100%;">
            <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2" :loading="logining"></el-button>
            <!--<el-button @click.native.prevent="handleReset2"></el-button>-->
        </el-form-item>
    </el-form>
</template>

<script>

    import {setTokenCookie} from "../api/cookie";

    export default {
        data() {
            return {
                logining: false,
                ruleForm2: {
                  account: '',
                  checkPass: '',
                  checked: false
                },
                rules2: {
                    account: [
                        { required: true, message: '', trigger: 'blur' },
                        //{ validator: validaePass }
                    ],
                    checkPass: [
                        { required: true, message: '', trigger: 'blur' },
                        //{ validator: validaePass2 }
                    ]
                },
                checked: true
            };
        },
        created () {
          // 
          let user = this.userApi.getUserCookie();
          user = JSON.parse(user);
          if(user != null && user.name != ""){
            this.ruleForm2.account = user.userAccount;
            this.ruleForm2.checkPass = user.userPass;
            this.ruleForm2.checked = user.remeberFlag;
          }

        },
        methods: {
            handleReset2() {
                this.$refs.ruleForm2.resetFields();
            },
            handleSubmit2(ev) {
                var _this = this;
                this.$refs.ruleForm2.validate((valid) => {
                  if (valid){
                    this.$axios.post("/login", {account:this.ruleForm2.account,password:this.ruleForm2.checkPass})
                      .then(data => {
                        // ,
                        if (data.status != 200) {
                          this.$message.error(''+data.status);
                          // this.$message({
                          //   message:'',
                          //   type:'warn'
                          // });
                        } else {
                          if (data.data.loginFlag == 'success'){
                            // VuextrueuserLoginfalse
                            //this.$store.dispatch("userLogin", true);
                            // VuexuserLoginfalseHTML5
                            // FlagisLoginFlagisLogin
                            //localStorage.setItem("loginFlag", "isLogin");
                            sessionStorage.setItem("auth", data.data.userFlag);
                            // token
                            this.cookieApi.setTokenCookie( data.data.token);
                            // token
                            console.log(data.data.token);
                            this.$axios.defaults.headers.post['Authorization'] = 'bearer ' + data.data.token;
                            sessionStorage.setItem("user", JSON.stringify({name:data.data.userName}));
                            // 
                            this.$message.success('');
                            // 
                            // cookie
                            if (this.checked === true){
                              // cookie,3
                              // cookieApimain.js
                              this.userApi.setUserCookie(this.ruleForm2.account, this.ruleForm2.checkPass, 7, true);
                            }else {
                              // cookie
                              this.userApi.setUserCookie("", "", -1, ""); //21
                            }
                            this.$router.push("/" + data.data.userFlag);
                          } else {
                            this.$message.error('');
                          }

                        }
                      });
                  }

                });
            },

        }
    }

</script>
<style >
    .login-container {
        -webkit-border-radius: 5px;
        border-radius: 5px;
        -moz-border-radius: 5px;
        background-clip: padding-box;
        margin: 180px auto;
        width: 350px;
        padding: 35px 35px 15px 35px;
        background: #fff;
        border: 1px solid #eaeaea;
        box-shadow: 0 0 25px #cac6c6;
    }
    .title {
        margin: 0px auto 40px auto;
        text-align: center;
        color: #505458;
    }
    .remember {
        margin: 0px 0px 35px 0px;
    }
</style>




