<template>

  <div class="app-container">
    <p></p>
    <el-form ref="form" :model="form" label-width="180px">

      <el-form-item label="">
        <el-col :span="20">
          {{regist.id}}
        </el-col>
      </el-form-item>

      <el-form-item label="">
        <el-col :span="20">
          {{regist.userAccount}}
        </el-col>
      </el-form-item>

      <el-form-item label="">
        <el-col :span="20">
          {{regist.userName}}
        </el-col>
      </el-form-item>

      <el-form-item label="">
        <el-col :span="20">
          {{regist.class}}
        </el-col>
      </el-form-item>

      <el-form-item label="">
        <el-col :span="20">
          {{regist.others}}
        </el-col>
      </el-form-item>

      <el-form-item label="">
        <el-col :span="20">
          <!-- autosize  autosize -->
          <el-input v-model="form.text"
                    :autosize="{ minRows: 5, maxRows: 20}"
                    type="textarea"/>
        </el-col>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button @click="onCancel"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        regist: {},
        form: {}
      }
    },
    created(){
      this.$axios.post("/service/getRegist", {})
        .then(data => {
          if (data.data.status === "2000") {
            this.$message('');
            this.regist = data.data;
            // console.log(this.regist);
          } else {
            this.$message({
              message: "",
              type: 'error'
            });
            this.$router.push('/service/regist_online');
            // 
          }
          }
        ).catch(err=>{
        this.$message({
          message: "",
          type: 'error'
        });
          this.$router.push('/service/regist_online');
          // 
      });
    },
    methods: {
      onSubmit() {
        // this.$refs.newupload.submit();
        if(this.regist.id!=null && this.form.text!=null){
          this.$axios.post("/service/postRegist", {id: this.regist.id, result: this.form.text})
            .then(data => {
              if (data.data.status === "2000") {
                this.$message('');
              } else {
                this.$message({
                  message: data.data.message,
                  type: 'error'
                });
              }
            });
        }else{
          this.openWarn("")
        }

      },
      onCancel() {
        this.$message({
          message: '',
          type: 'warning'
        });
        this.form = {};
      },
      openWarn(message) {
        this.$alert(message, '', {
          confirmButtonText: '',
          callback: action => {
            // 
          }
        });
      },
      openError(message) {
        this.$alert(message, '', {
          confirmButtonText: '',
          callback: action => {
            // 
          }
        });
      },
    },

  }
</script>

<style scoped>
  .line{
    text-align: center;
  }
</style>




