<template>

  <div class="app-container">
    <p></p>
    <el-form ref="form" :model="form" label-width="120px">

      <el-form-item label="" >
        <!---->
        <el-col :span="20">
          <el-input v-model="form.account"/>
        </el-col>
      </el-form-item>

      <el-form-item label="">
        <el-col :span="20">
          <el-input v-model="form.name"/>
        </el-col>
      </el-form-item>

      <el-form-item label="">
        <el-col :span="20">
          <el-input v-model="form.pass"/>
        </el-col>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button @click="onCancel"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        form: {}
      }
    },
    methods: {
      onSubmit() {
        if (this.form.account != null && this.form.name != null && this.form.pass != null) {
          this.$axios.post("/admin/addDoctor", {account: this.form.account, name: this.form.name, pass: this.form.pass})
            .then(data => {
              if (data.data.status === "2000") {
                this.$message('');
                this.form = {};
              } else {
                // this.$message({
                //   message: data.data.message,
                //   type: 'error'
                // });
                this.openError(data.data.message);
              }
            }).catch(()=>{this.openError("");});
        }else {
          this.openWarn('');
        }
      },
      onCancel() {
        this.$message({
          message: '',
          type: 'warning'
        });
        this.form = {};
      },
      openWarn(message) {
        this.$alert(message, '', {
          confirmButtonText: '',
          callback: action => {
            // 
          }
        });
      },
      openError(message) {
        this.$alert(message, '', {
          confirmButtonText: '',
          callback: action => {
            // 
          }
        });
      },
    }
  }
</script>

<style scoped>
  .line{
    text-align: center;
  }
</style>




