var websock = null;
var global_callback = ()=>{};
var serverPort = '8082';	//webSocket

// ws
function getWebIP(){
  return 'localhost';
}

// weosocket
function initWebSocket(params){
  //ws
  var wsuri = "ws://" +getWebIP()+ ":" + serverPort + '/ws' + "?" + params;
  websock = new WebSocket(wsuri);
  websock.onmessage = function(e){
    websocketonmessage(e);
  };
  websock.onclose = function(e){
    websocketclose(e);
  };
  websock.onopen = function () {
    websocketOpen();
  };

  //
  websock.onerror = function () {
    this.$message.error("WebSocket");
  }
}

//  ,
function sendSock(agentData){
  // global_callback = callback;
  if (websock.readyState === websock.OPEN) {
    //ws
    websocketsend(agentData)
  }else if (websock.readyState === websock.CONNECTING) {
    //  1s
    setTimeout(function () {
      sendSock(agentData);
    }, 1000);
  }else {
    //  1s
    setTimeout(function () {
      sendSock(agentData);
    }, 1000);
  }
}

// 
function setCallback(callback) {
  global_callback = callback;
}

// 
function websocketonmessage(e){
  // console.log(global_callback);
  global_callback(e.data);
}

// 
function websocketsend(agentData){
  websock.send(JSON.stringify(agentData));
}

// 
function websocketclose(e){
  console.log("connection closed (" + e.code + ")");
}

function websocketOpen(e){
  console.log("");
}

// initWebSocket();

export{sendSock, initWebSocket, setCallback}



