<template>
  <div class="app-container">
    <p></p>
    <el-table
      v-loading="listLoading"
      :data="list"
      element-loading-text="Loading"
      border
      fit
      highlight-current-row>
      <el-table-column align="center" label="">
        <template slot-scope="scope">
          {{ scope.row.acount }}
        </template>
      </el-table-column>
      <el-table-column label=""  align="center">
        <template slot-scope="scope">
          {{ scope.row.name }}
        </template>
      </el-table-column>
      <el-table-column label=""  align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.registersNum }}</span>
        </template>
      </el-table-column>
      <el-table-column label=""  align="center">
        <template slot-scope="scope">
          {{ scope.row.lecturesNum }}
        </template>
      </el-table-column>
      <el-table-column label=""  align="center">
        <template slot-scope="scope">
          {{ scope.row.recommendNum }}
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  // import { getList } from '@/api/table'

  export default {
    filters: {
      statusFilter(status) {
        const statusMap = {
          published: 'success',
          draft: 'gray',
          deleted: 'danger'
        };
        return statusMap[status]
      }
    },
    data() {
      return {
        list: null,
        listLoading: true
      }
    },
    created() {
      // this.fetchData()
      this.socketApi.setCallback(this.setSession);
      // 
      this.socketApi.sendSock({code:"2007", message:"getServiceOnLineList"})
    },
    methods: {
      // websocket
      setSession(data){
        data = JSON.parse(data);
        if (data.code === "2007") {
          this.listLoading = true;
          this.list = data.data.items;
          this.listLoading = false;
          // 
          this.socketApi.sendSock({"code":"2007","message":""});
        }
      }
    }
  }
</script>



