<template>
  <div class="app-container">
    <p></p>
    <el-table
      v-loading="listLoading"
      :data="list"
      element-loading-text="Loading"
      border
      fit
      highlight-current-row>
      <el-table-column label=""  align="center">
        <template slot-scope="scope">
          {{ scope.row.account }}
        </template>
      </el-table-column>
      <el-table-column label=""  align="center">
        <template slot-scope="scope">
          {{ scope.row.name }}
        </template>
      </el-table-column>
      <el-table-column label=""  align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.others }}</span>
        </template>
      </el-table-column>

      <el-table-column label=""  align="center">
        <template slot-scope="scope">
          <el-select v-model="scope.row.doctorAccount" placeholder="">
            <!--:v-for-->
            <el-option v-for="doctor in doctorList"  :label="doctor.name" :value="doctor.account" :key="doctor.name"></el-option>
          </el-select>
        </template>
      </el-table-column>

      <el-table-column label=""  align="center">
        <template slot-scope="scope">
          <el-button type="primary" size="small" icon="el-icon-edit" @click="postRegist(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

  </div>
</template>

<script>
  // import { getList } from '@/api/table'

  export default {
    filters: {
      statusFilter(status) {
        const statusMap = {
          published: 'success',
          draft: 'gray',
          deleted: 'danger'
        };
        return statusMap[status]
      }
    },
    data() {
      return {
        list: null,
        listLoading: true,
        doctorList: null,
      }
    },
    created() {
      // this.fetchData()
      this.socketApi.setCallback(this.setSession);
      // 
      this.socketApi.sendSock({"code":"2009", "message":" getDoctorList "});
      this.socketApi.sendSock({"code":"2010", "message":" getWaitList "});
    },
    methods: {
      // websocket
      setSession(data){
        data = JSON.parse(data);
        if (data.code === "2009") {
          // 
          this.doctorList = data.data.items;
          // 
          this.socketApi.sendSock({"code":"2009","message":""});
        }else if(data.code === "2010") {
          // 
          this.listLoading = true;
          this.list = data.data.items;
          this.listLoading = false;
          // 
          this.socketApi.sendSock({"code":"2010","message":""});
        }
      },
      postRegist(row) {
        // console.log(JSON.parse(this.userApi.getUserCookie()))
        if(row.doctorAccount!=null){
          this.$axios.post("/service/setWebChat", { userAccount: row.account, doctorAccount: row.doctorAccount})
            .then(data => {
              if (data.data.status === "2000") {
                this.openWarn('');
              } else {
                this.openError(data.data.message);
              }
            }).catch(()=>{this.openError("");});
        }else {
          this.openWarn("")
        }

      },
      openWarn(message) {
        this.$alert(message, '', {
          confirmButtonText: '',
          callback: action => {
            // 
          }
        });
      },
      openError(message) {
        this.$alert(message, '', {
          confirmButtonText: '',
          callback: action => {
            // 
          }
        });
      },
    }
  }
</script>



