<template>

  <div class="app-container">
    <p></p>
    <el-form ref="form" :model="form" label-width="180px">

      <el-form-item label="()">
        <!---->
        <el-col :span="20">
          <el-input v-model="form.title"  placeholder="" />
        </el-col>
      </el-form-item>

      <el-form-item label="">
        <el-radio-group v-model="form.type">
          <el-radio :label="1"></el-radio>
          <el-radio :label="2"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="()">
        <el-col :span="20">
          <!---->
          <el-upload
            class="upload-file"
            drag
            accept=".mp4, .mp3, .wav"
            :limit=1
            list-type="picture-card"
            :action="doUpload()"
            :before-upload="beforeUpload"
            ref="newupload"
            multiple
            :auto-upload="false">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text"><em></em></div>
          </el-upload>
        </el-col>
      </el-form-item>

      <el-form-item label="">
        <el-progress v-if="videoFlag == true"
                     type="circle"
                     :percentage="videoUploadPercent"
                     style="margin-top:30px;">
        </el-progress>
      </el-form-item>




      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button @click="onCancel"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        form: {type: 1},

        videoFlag:false
      }
    },
    methods: {
      onSubmit() {
        this.$refs.newupload.submit();
        // this.$axios.post("/admin/addDoctor", {account: this.form.account, name: this.form.name, pass: this.form.pass})
        //   .then(data => {
        //     if (data.data.status === 2000) {
        //       this.$message('');
        //     } else {
        //       this.$message({
        //         message: data.data.message,
        //         type: 'error'
        //       });
        //     }
        //   });
      },
      onCancel() {
        this.$message({
          message: '',
          type: 'warning'
        });
        this.form = {};
      },
      beforeUpload(file){
        if (file!=null && this.form.title!=null){
          let fd = new FormData();
          fd.append('file',file);//
          fd.append('title',this.form.title);//
          if (this.form.type === 1){
            // 
            this.$axios.post('/service/uploadLecturevideo',fd).then(function(res){
              this.openWarn("");
            }).catch(
              // (e)=>{this.openError(e.toString());}
              );
          }else if (this.form.type === 2) {
            this.$axios.post('/service/uploadLectureaudio',fd).then(function(res){
            this.openWarn("");
            }).catch(
              // (e)=>{this.openError(e.toString());}
              );
          }
        } else {
          this.openWarn("");
        }

      },
      newSubmitForm(){
        //
        this.$refs.newupload.submit();
      },
      doUpload(){
        // url
        return "/useless";
      },
      //
      uploadVideoProcess (event, file, fileList) {
        this.videoFlag = true;
        this.videoUploadPercent = file.percentage.toFixed(0) * 1;
      },
      openWarn(message) {
        this.$alert(message, '', {
          confirmButtonText: '',
          callback: action => {
            // 
          }
        });
      },
      openError(message) {
        this.$alert(message, '', {
          confirmButtonText: '',
          callback: action => {
            // 
          }
        });
      },
    },

  }
</script>

<style scoped>
  .line{
    text-align: center;
  }
</style>




