import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Welcome from '@/components/Welcome'
import Admin from '@/components/Admin'
import Doctor from '@/components/Doctor'
import Service from '@/components/Service'

// 
import Servicelist from '@/components/admin/ServiceList'
import Addservice from '@/components/admin/Addservice'
import Adddoctor from '@/components/admin/Adddoctor'
import Changedoctor from '@/components/admin/Changedoctor'
import Changeaccount from '@/components/admin/Changeaccount'
import Serviceonlinelist from '@/components/admin/Serviceonlinelist'
import Servicequery from '@/components/admin/Servicequery'

// 
import ChangeDoctor_s from '@/components/service/Changedoctor'
import PostRecommendation from '@/components/service/PostRecommendation'
import PostRegist from '@/components/service/PostRegist'
import RegistOnLine from '@/components/service/RegistOnLine'
import UploadLecture from '@/components/service/UploadLecture'
import UploadTextLecture from '@/components/service/UploadTextLecture'
import WebChatRegist from '@/components/service/WebChatRegist'
import DoctorOnLine from '@/components/service/DoctorOnLine'

// 
import UserList from '@/components/doctor/UserList'
import PostReport from '@/components/doctor/PostReport'
import WebChat from '@/components/doctor/WebChat'


Vue.use(Router);

export default new Router({
  // '/#' 
  // mode: 'history',
  routes: [
    // 
    {
      path: '/',
      name: '',
      component: Login,
      meta:{
        isLogin:false
      }
    },
    // 
    {
      path: '/login',
      component: Login,
      name: '',
      meta:{
        isLogin:false
      }
    },
    // iconCls  http://fontawesome.dashgame.com/
    {
      path: '/admin', component:Admin, name: '', iconCls: 'fa fa-address-card',
          meta:{ isLogin:true, auth: 'admin'},
          children:[
            {path: '/admin/add_service', component: Addservice, name: '', meta:{ isLogin:true, auth: 'admin'}},
            {path: '/admin/add_doctor', component: Adddoctor, name: '', meta:{ isLogin:true, auth: 'admin'}},
            {path: '/admin/change_account', component: Changeaccount, name: '', meta:{ isLogin:true, auth: 'admin'}},
            {path: '/admin/change_doctor', component: Changedoctor, name: '', meta:{ isLogin:true, auth: 'admin'}},
            ]
    },
    { path: '/admin/', component: Admin, name: '', iconCls: 'fa fa-bar-chart',
          meta:{isLogin:true, auth: 'admin'},
          children:[
            {path: '/admin/service_list1', component: Servicelist, name: '', meta:{ isLogin:true, auth: 'admin'}},
            {path: '/admin/service_list2', component: Serviceonlinelist, name: '', meta:{ isLogin:true, auth: 'admin'}},
            {path: '/admin/service_list3', component: Servicequery, name: '', meta:{ isLogin:true, auth: 'admin'}},
            ]
    },
    // 
    {
      path: '/service', component:Service, name: '', iconCls: 'fa fa-edit',
      meta:{ isLogin:true, auth: 'service'},
      children:[
        {path: '/service/upload_lecture', component: UploadLecture, name: '', meta:{ isLogin:true, auth: 'service'}},
        {path: '/service/upload_tlecture', component: UploadTextLecture, name: '', meta:{ isLogin:true, auth: 'service'}},
        {path: '/service/upload_recommendation', component: PostRecommendation, name: '', meta:{ isLogin:true, auth: 'service'}},
        {path: '/service/change_doctor', component: ChangeDoctor_s, name: '', meta:{ isLogin:true, auth: 'service'}},
      ]
    },
    { path: '/service', component: Service, name: '', iconCls: 'fa fa-braille',
      meta:{isLogin:true, auth: 'service'},
      children:[
        {path: '/service/regist_online', component: RegistOnLine, name: '', meta:{ isLogin:true, auth: 'service'}},
        {path: '/admin/post_regist', component: PostRegist, name: '', meta:{ isLogin:true, auth: 'service'}}
      ]
    },
    { path: '/service', component: Service, name: '', iconCls: 'fa fa-comments',
      meta:{isLogin:true, auth: 'service'},
      children:[
        {path: '/service/service_list1', component: WebChatRegist, name: '', meta:{ isLogin:true, auth: 'service'}},
        {path: '/service/service_list2', component: DoctorOnLine, name: '', meta:{ isLogin:true, auth: 'service'}}
      ]
    },
    // 
    { path: '/doctor', component: Doctor, name: '', iconCls: 'fa fa-edit',
      meta:{isLogin:true, auth: 'doctor'},
      children:[
        {path: '/doctor/user_list', component: UserList, name: '', meta:{ isLogin:true, auth: 'doctor'}},
        {path: '/doctor/post_report', component: PostReport, name: '', meta:{ isLogin:true, auth: 'doctor'}}
      ]
    },
    { path: '/doctor', component: Doctor, name: '', iconCls: 'fa fa-comments',
      meta:{isLogin:true, auth: 'doctor'},
      children:[
        {path: '/doctor/webchat/', component: WebChat, name: '', meta:{ isLogin:true, auth: 'doctor'}}
      ]
    },
    // // 
    // {
    //   path: '/Admin/Addaccount',
    //   component: Addaccount,
    //   name: '/',
    //   iconCls: 'fa fa-address-card',
    //   meta:{
    //     isLogin:true,
    //     auth: 'admin'
    //   }
    // },
    // // 
    // {
    //   path: '/Admin/ServiceList',
    //   component: ServiceList,
    //   name: '',
    //   iconCls: 'fa fa-bar-chart',
    //   meta:{
    //     isLogin:true,
    //     auth: 'admin'
    //   }
    // }
  ]
})



